import { Notification } from 'element-ui'
function wrapService(service) {
    const $service = {}
    Object.keys(service).forEach((key) => {
        $service[key] = async (args, message) => {
            return service[key](args)
                .then((result) => {
                    if (message) {
                        Notification({
                            title: '',
                            type: 'success',
                            message
                        })
                    }
                    return result
                })
                .catch((error) => {
                    Notification({
                        title: '',
                        type: 'error',
                        message: error.message
                    })
                })
        }
    })
    return $service
}

export default wrapService